import {observable, computed, action, autorun} from 'mobx';
import swal from 'sweetalert'
import '../../lib/jquery.min'
import '../../lib/jquery.xctips'
import '../styles/jquery.xctips.css'

class appleStore {

    @observable apples = [
        {
            id: 1,
            weight: 235,
            isEaten: true
        },
        {
            id: 2,
            weight: 256,
            isEaten: false
        }
    ];

    @observable isPicking = false;
    @observable buttonText = '';
    @observable max_apples = 99;

    /**   */
    @computed get status(){
        let status = {
            appleNow: {
                quantity: 0,
                weight: 0
            },
            appleEaten: {
                quantity: 0,
                weight: 0
            }
        };
        this.apples.forEach(apple => {
            let selector = apple.isEaten ? 'appleEaten':'appleNow';
            status[selector].quantity++;
            status[selector].weight += apple.weight;
        });
        return status;
    }


    /**/
    @action pickApple = ()=>{

        /** thunk,  */
        if (this.isPicking) {
            console.log('thunk!');
            return;
        }

        if (this.status['appleNow'].quantity >= 10) {
            xcsoft.error('!', 3000);
            return;
        }

        if (this.apples.length >= this.max_apples) {
            xcsoft.error('', 3000);
            return;
        }

        this.isPicking = true;
        this.buttonText = '...';
        let url = 'https://caihenry.github.io/apple-basket-redux/data/fetch_apple.json';
        if(self.fetch) {
            //  fetch 
            fetch(url)
                .then(res => {
                    /* mock */
                    let weight = Math.floor(200 + Math.random() * 50);
                    let new_apple = {
                        id: this.apples.length + 1,
                        weight: weight,
                        isEaten: false
                    };
                    this.apples.push(new_apple);
                    console.log('fetchid: '+new_apple.id);
                }).catch((e) => {
                xcsoft.error('', 3000);
            });
        } else {
            //  XMLHttpRequest 
            var _this = this;
            let request = new XMLHttpRequest();
            request.open("GET", url);
            request.onload = function() {
                if(request.status == 200) {
                    let text = request.responseText;
                    let object = JSON.parse(text);
                    /* mock */
                    let weight = Math.floor(200 + Math.random() * 50);
                    let new_apple = {
                        id: _this.apples.length,
                        weight: weight,
                        isEaten: false
                    };
                    _this.apples.push(new_apple);
                    console.log('requestid: '+new_apple.id);
                } else {
                    xcsoft.error('request error: ' + request.status + ', url: ' + request.url + '.');
                }
            };
            request.onerror = function() {
                xcsoft.error('request,', 3000);
            };
            request.send(null);
        }
        this.isPicking = false;
        this.buttonText = '';
    };

    /* */
    @action eatApple = (appleId)=>{
        console.log('id: '+appleId);
        this.apples.some(function(apple){
            if(apple.id == appleId){
                apple.isEaten = true;
                console.log('id: '+apple.id);
                return true;
            }
        });
    };

    showAbout = () => {
        /* refer to https://sweetalert.js.org/guides/#getting-started */
        swal({
            title: '',
            text: ' forked from https://github.com/ckinmind/apple-basket-redux\n:  ckinmind&Henry Cai\r\n: imhenrycai@yahoo.com\r\n: 2018/02/16\r\n: v1.0',
            //icon: 'success',
            button: '',
            className: 'about'
        });
    }
}

export default appleStore;


