import React from 'react';
import '../styles/appleItem.scss';
import {observer} from 'mobx-react';

@observer
class AppleItem extends React.Component {

    render() {
        let { apple, eatApple } = this.props;
        let pad = (num, count) => {
            return new Array( count - ( '' + num ).length + 1 ).join( '0' ) + num;
        };

        return (
            <div className="appleItem">
                <div className="apple">
                    <img src={require('../images/apple.png')} alt="" title=""/>
                </div>
                <div className="info">
                    <div className="name"> - { pad(apple.id, 2) }</div>
                    <div className="weight">{ apple.weight }</div>
                </div>
                <div className="btn-div">
                    <button title="" onClick={()=>eatApple(apple.id)}>  </button>
                </div>
            </div>
        );
    }
}

AppleItem.propTypes = {
    // bindActionCreatorsdispatch(action)
    eatApple: React.PropTypes.func.isRequired,
    apple: React.PropTypes.object.isRequired     // 
};


export default AppleItem;


